import React, { forwardRef, useEffect, useState } from "react";
import {
  ChatBubbleOutline,
  FavoriteBorder,
  Favorite,
  PublishOutlined,
  Repeat,
  VerifiedUser,
} from "@mui/icons-material";
import { Avatar, Button } from "@mui/material";
import {
  doc,
  updateDoc,
  arrayUnion,
  arrayRemove,
  serverTimestamp,
  collection,
  addDoc,
  deleteDoc,
  onSnapshot,
  query,
  orderBy,
} from "firebase/firestore";
import db from "../../firebase";
import "./Post.css";

/**
 * 
 *   Firebase Auth 
 */
const loggedInUserId = "testUserId";
const loggedInDisplayName = "";

const Post = forwardRef(
  (
    {
      id, // ID (ID)
      displayName,
      username,
      verified,
      text,
      image,
      avatar,
      tags = [],
      likes = [],
    },
    ref
  ) => {
    // 
    const isLiked = likes.includes(loggedInUserId);

    // /
    const [showComments, setShowComments] = useState(false);

    // comments
    const [comments, setComments] = useState([]);

    // 
    const [commentText, setCommentText] = useState("");

    /**
     * ---------------------------
     *  1. 
     * ---------------------------
     */
    useEffect(() => {
      // : posts/{id}/comments  (timestamp  )
      const commentsRef = collection(db, "posts", id, "comments");
      const q = query(commentsRef, orderBy("timestamp", "asc"));

      const unsubscribe = onSnapshot(q, (snapshot) => {
        const newComments = snapshot.docs.map((doc) => ({
          id: doc.id, // ID
          ...doc.data(),
        }));
        setComments(newComments);
      });

      // 
      return () => unsubscribe();
    }, [id]);

    /**
     * ---------------------------
     *  2. 
     * ---------------------------
     */
    const handleLike = async () => {
      const postRef = doc(db, "posts", id);

      if (isLiked) {
        //   
        await updateDoc(postRef, {
          likes: arrayRemove(loggedInUserId),
        });
      } else {
        //   
        await updateDoc(postRef, {
          likes: arrayUnion(loggedInUserId),
        });
      }
    };

    /**
     * ---------------------------
     *  3. 
     * ---------------------------
     */
    const handleCommentSubmit = async (e) => {
      e.preventDefault();
      if (!commentText.trim()) return; // 

      // 
      const commentsRef = collection(db, "posts", id, "comments");
      await addDoc(commentsRef, {
        userId: loggedInUserId,
        displayName: loggedInDisplayName,
        text: commentText,
        timestamp: serverTimestamp(), // 
      });

      // 
      setCommentText("");
    };

    /**
     * ---------------------------
     *  4. 
     *    ( or )
     * ---------------------------
     */
    const handleDeleteComment = async (commentDocId, commentUserId) => {
      //   
      const canDelete =
        commentUserId === loggedInUserId || username === loggedInUserId;
      if (!canDelete) return;

      await deleteDoc(doc(db, "posts", id, "comments", commentDocId));
    };

    // 
    const canDeleteComment = (commentUserId) => {
      return commentUserId === loggedInUserId || username === loggedInUserId;
    };

    return (
      <div className="post" ref={ref}>
        {/*  */}
        <div className="post--avatar">
          <Avatar src={avatar} />
        </div>

        {/*  */}
        <div className="post--body">
          <div className="post--header">
            <div className="post--headerText">
              <h3>
                {displayName}{" "}
                <span className="post--headerSpecial">
                  {verified && <VerifiedUser className="post--badge" />}@
                  {username}
                </span>
              </h3>
            </div>
            <div className="post--headerDescription">
              <p>{text}</p>
            </div>
          </div>

          {/*  */}
          {image && <img src={image} alt="" />}

          {/*  */}
          <div className="post--tags">
            {tags.map((tag, index) => (
              <span key={index} className="post--tag">
                {tag}
              </span>
            ))}
          </div>

          {/*  */}
          <div className="post--footer">
            {/*  */}
            <div onClick={handleLike} style={{ cursor: "pointer" }}>
              {isLiked ? (
                <Favorite style={{ color: "red" }} fontSize="small" />
              ) : (
                <FavoriteBorder fontSize="small" />
              )}
              <span style={{ marginLeft: "4px", fontSize: "12px" }}>
                {likes.length}
              </span>
            </div>

            {/* / */}
            <div
              style={{ cursor: "pointer" }}
              onClick={() => setShowComments((prev) => !prev)}
            >
              <ChatBubbleOutline fontSize="small" />
              <span style={{ marginLeft: "4px", fontSize: "12px" }}>
                {showComments ? "" : ""}
              </span>
            </div>

            <Repeat fontSize="small" />
            <PublishOutlined fontSize="small" />
          </div>

          {/* showComments  true  */}
          {showComments && (
            <>
              {/*  */}
              <div className="post--comments">
                <h4></h4>
                {comments.map((comment) => (
                  <div key={comment.id} className="post--comment">
                    <div className="post--commentHeader">
                      <span>
                        {comment.displayName} (
                        {comment.userId === username ? "" : ""})
                      </span>
                      {canDeleteComment(comment.userId) && (
                        <button
                          className="post--commentDeleteBtn"
                          onClick={() =>
                            handleDeleteComment(comment.id, comment.userId)
                          }
                        >
                          
                        </button>
                      )}
                    </div>
                    <div className="post--commentBody">{comment.text}</div>
                  </div>
                ))}
              </div>

              {/*  */}
              <form
                onSubmit={handleCommentSubmit}
                style={{ marginTop: "10px" }}
              >
                <textarea
                  placeholder="..."
                  value={commentText}
                  onChange={(e) => setCommentText(e.target.value)}
                  rows={5}
                  style={{
                    width: "100%",
                    padding: "8px",
                    borderRadius: "6px",
                    outline: "none",
                    border: "1px solid #ccc",
                    resize: "vertical",
                  }}
                />
                <Button
                  variant="contained"
                  type="submit"
                  style={{ marginTop: "8px" }}
                >
                  
                </Button>
              </form>
            </>
          )}
        </div>
      </div>
    );
  }
);

export default Post;
